import { Component } from '@angular/core';
@Component ({
    selector: 'app-sign-in',
    template: `
    <form (ngSubmit) = "onSubmit(formSignIn);" #formSignIn = "ngForm">
        <input 
            placeholder="Email" 
            ngModel 
            #txtEmail = "ngModel" 
            name = "email" 
            required
            email
        >
        <p *ngIf = "txtEmail.touched && txtEmail?.errors?.required">
            Email is required.
        </p>
        <p *ngIf = "txtEmail.touched && txtEmail?.errors?.email">
            Email is not valid.
        </p>
        <br><br>
        <input 
            type = "password" 
            placeholder="Psassword" 
            ngModel 
            #txtPassword = "ngModel"
            name = "password"
            minlength = "6"
            pattern = "[a-z0-9]*"
        >
            
        <br><br>
        <div ngModelGroup = "subject">
            <p> <input type = "checkbox" [ngModel]="false" name = "angular">Angular </p>
            <p> <input type = "checkbox" [ngModel]="false" name = "Swift">Swift</p>
            <p> <input type = "checkbox" [ngModel]="false" name = "Js">Js</p>
       </div>
        <button [disabled] = "formSignIn.invalid">Submit</button>
    </form>
    <p> {{txtEmail.errors | json}} </p>
    <p> {{txtPassword.errors | json }}</p>
    <p> {{formSignIn.value | json }}</p>
    `
})
export class SigninComponent {

    onSubmit(formSignIn){
        console.log(formSignIn.value);
        // throw new Error('Form is invalided');

    }
}


