import { Component} from "@angular/core";
@Component ({
    selector: "app-baihat",
    templateUrl: "./baihat.component.html"
})

export class BaihatComponent {
    tenBH: string = "Demons 10";
    single: string = "Dam Vinh Hung";
    arrPeople = [
        {name: 'Ti', age: 100},
        {name: 'Teo', age: 10}, 
        {name: 'Tun', age: 5}
    ];
    removeElementByName(name: string){
        const index = this.arrPeople.findIndex(a=>a.name === name);
        this.arrPeople.splice(index, 1);
    }
}


