import { Component} from '@angular/core';
import { IpService} from './ip.service';
@Component({
    selector: 'app-ip',
    template: '<h3>{{m}}</h3>',
    providers: [IpService]
})
export class IpComponent {
    m: String;
    constructor(private ipService: IpService) {
        this.ipService.getIp()
        .then(m=>this.m = m)
    }
}


