import {Component, Input, Output, EventEmitter} from "@angular/core";
@Component ({
    selector: "bai1",
    templateUrl: "./bai1.component.html"
})

export class Bai1Component {
    @Input() name: string;
    @Input() age: number;
    @Output() removeByName = new EventEmitter();
    bien: number = 0;
    remove(){
        this.removeByName.emit(this.name);
    }
    CONG(){
        this.bien += 1;
    }
    TRU(){
        this.bien -= 1;
    }
    changeIMG(a){
        a.srcElement.src = "https://cdn1.iconfinder.com/data/icons/ninja-things-1/1772/ninja-simple-512.png";
    }
    changeIMG1(a){
        a.srcElement.src = "http://dl.hiapphere.com/data/icon/201705/HiAppHere_com_com.leomaz.flix.png";
    }

}


