import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators} from '@angular/forms';
@Component({
    selector: 'app-sign-up',
    template: `
        <form (ngSubmit) = "onSubmit()" [formGroup] = "formSignUp">
            <input placeholder = "Email" formControlName = "email">
            <p *ngIf = "formSignUp.get('email').invalid && formSignUp?.get('email').touched">
                Email is required!
            </p>
            <br><br>
            <input type = "password" placeholder = "Password" formControlName = "password">
            <br><br>
           <div formGroupName = "subject">
                <label><input type = "checkbox" formControlName = "angular">Angular</label>
                <label><input type = "checkbox" formControlName = "js">Js</label>
                <label><input type = "checkbox" formControlName = "react">React</label>
            </div>
            <br><br>
            <button [disabled] = "formSignUp.invalid">Submit</button>
        </form>
        <code> {{formSignUp.get('email').errors | json}} </code>
    `
})
export class SignupComponent implements OnInit{
    //mô tả form qua thuộc tính formSignup thuôc class formgroup
    formSignUp: FormGroup;
    //khởi tạo giá trị formgroup tso khởi tạo là jsobject vs 2 thuộc tính email vs password 
    constructor(private fb: FormBuilder) { }
    ngOnInit(){
        this.formSignUp = this.fb.group({
            email: ['', [gmailValidator, Validators.email, Validators.minLength(6)]], 
            password: '',
            subject: this.fb.group({
                angular: true,
                js: false,
                react: false
            }),
          
        });
    }
    onSubmit(){
        console.log(this.formSignUp.value);
    }
}
function gmailValidator(formControl: FormControl){
    if(formControl.value.includes('@gmail.com')){
        return null;
    }
    return {gmail: true}
}


